#footer{
  width: 100%;
  background-color: #1b242f;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 5rem;
  margin-top: -1rem;
}

#footer .icon{
  width: 5rem;
  height: 5rem;
  background-color: #444649;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 1.5rem;
}

div i{
  color: #fff;
  font-size: 2rem;
}

#footer .icon:hover{
  cursor: pointer;
  background-color: #04c2c9;
}

#footer .chevron{
  width: 5rem;
  height: 5rem;
  background-color: #e31b6d;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  left: 48%;
  margin-top: -20rem;
  cursor: pointer;
}
 .chevron i{
   font-size: 3rem;
 }


 .copywrite{
   color: #444649;
   position: absolute;
   margin-bottom: -12rem;
   display: flex;
   align-items: center;
   justify-content: center;
   font-size: 1.5rem;
   text-transform: uppercase;
   font-weight: 600;
 }

 .copywrite span{
   color: #e31b6d;
   font-size: 1.2rem;
   text-transform: uppercase;
   font-weight: 600;
 }


 








