 .hex-wrap {
  position: absolute;
  display: inline-block;
  display: flex;
  justify-content: space-evenly;
  width: 150px;
  height: 150px;
  left: 130px;
}

.hexagon {
  background-color: #04C2C9;
  height: 100%;
  width: calc(100% * 0.57735);
  display: inline-block;
}

.hexagon:before {
  position: absolute;
  top: 0;
  right: calc((100% / 2) - ((100% * 0.57735) / 2));
  background-color: inherit;
  height: inherit;
  width: inherit;
  content: '';
  transform: rotateZ(60deg);
}

.hexagon:after {
  position: absolute;
  top: 0;
  right: calc((100% / 2) - ((100% * 0.57735) / 2));
  background-color: inherit;
  height: inherit;
  width: inherit;
  content: '';
  transform: rotateZ(-60deg);
}

.hexagon i {
  position: absolute;
  top: 2rem;
  font-size: 50pt;
  color: #fff;
  left: 0;
  right: 0;
  z-index: 1;
}

.mdi-speedometer:before {
  content: "\F4C5";
}

.mdi {
  display: inline-block;
  font: normal normal normal 24px/1 "Material Design Icons";
  font-size: inherit;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  transform: translate(0, 0);
}












